.app-light {
  text-align: center;
  background-color: rgb(227, 253, 255);
  padding-left: 150px;
  padding-right: 150px;
}
.app-dark {
  text-align: center;
  background-color: rgb(15, 15, 15);
  padding-left: 150px;
  padding-right: 150px;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  background-color: rgb(0, 57, 75) !important;
}


.title {
  text-align: left;
  flex-grow: 1;
}

.navbar-button-light {

}
.navbar-button-dark {

}

.logo:hover {
  background-color: rgb(193, 224, 226);
}

.tool-button{
  background-color: rgb(0, 57, 75) !important;
  margin: 3px !important;
  color: white !important;
}

.tool-button:hover{
  background-color: rgb(1, 89, 116) !important;
  margin: 3px !important;
  color: white !important;
}

.section-light {
  min-height: 90vh !important;
  margin: 20px;
  background-color: rgb(227, 253, 255);
  min-height: 300px;
  margin: 20px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
}
.section-dark {
  min-height: 90vh !important;
  margin: 20px;
  background-color: rgb(15, 15, 15);
  color: white;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
}

.settings-light {
  min-height: 300px;
  margin-left: -150px;
  margin-right: -150px;
  background-color: rgb(175, 235, 240);
  padding-top: 5px;
  padding-bottom: 15px;
}
.settings-dark {
  min-height: 300px;
  margin: 20px;
  margin-left: -150px;
  margin-right: -150px;
  background-color: rgb(90, 90, 90);
  color: white;
  padding-top: 5px;
  padding-bottom: 15px;

}

.toggle-light {
  background-color: #79D7ED
}
.toggle-dark {
  background-color: #39598A
}

.footer-light {
  background-color:  rgb(0, 57, 75);
  min-height: 100px;
  margin: 0px;
  color: white;
  padding-bottom: 15px;

}
.footer-dark {
  background-color:  rgb(0, 57, 75);
  min-height: 100px;
  margin: 0px;
  color: white;
  padding-bottom: 15px;
}

